import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import Link from 'next/link';
import Image from 'next/image';
import { CalendarDays, ArrowLeft } from 'lucide-react';

// Placeholder function to get article data by slug
async function getArticleData(slug: string) {
  // In a real app, fetch this from a CMS or database
  // For now, return placeholder data
  if (slug === '1' || slug === '2' || slug === '3') { // Match placeholder IDs from news list
    return {
      slug,
      title: `Título del Artículo ${slug}`,
      date: '2024-10-28',
      image: `https://placehold.co/800x400/00723F/FFFFFF?text=Articulo+${slug}`,
      aiHint: 'news article content',
      content: `
        <p class="mb-4">Este es el contenido completo del artículo ${slug}. Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</p>
        <p class="mb-4">Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</p>
        <h3 class="text-xl font-semibold mt-6 mb-3 text-primary">Subtítulo del Artículo</h3>
        <p class="mb-4">Curabitur pretium tincidunt lacus. Nulla gravida orci a odio. Nullam varius, turpis et commodo pharetra, est eros bibendum elit, nec luctus magna felis sollicitudin mauris. Integer in mauris eu nibh euismod gravida.</p>
        <ul class="list-disc list-inside mb-4 pl-4">
          <li>Elemento de lista uno</li>
          <li>Elemento de lista dos</li>
          <li>Elemento de lista tres</li>
        </ul>
        <p>Más contenido aquí para rellenar el artículo y hacerlo parecer más completo.</p>
      `,
      category: 'Convocatorias', // Example category
    };
  }
  return null;
}

export default async function ArticlePage({ params }: { params: { slug: string } }) {
  const article = await getArticleData(params.slug);

  if (!article) {
    return (
      <div className="container mx-auto px-4 md:px-6 py-16 text-center">
        <h1 className="text-3xl font-bold text-destructive mb-4">Artículo no encontrado</h1>
        <p className="text-lg text-muted-foreground mb-6">El artículo que buscas no existe o ha sido movido.</p>
        <Button asChild>
          <Link href="/noticias"><ArrowLeft className="mr-2 h-4 w-4" />Volver a Noticias</Link>
        </Button>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 md:px-6 py-12 md:py-16">
      <article className="max-w-3xl mx-auto">
        <header className="mb-8">
          <Button variant="outline" asChild className="mb-6">
            <Link href="/noticias"><ArrowLeft className="mr-2 h-4 w-4" />Volver a Noticias</Link>
          </Button>
          <h1 className="font-headline text-3xl md:text-4xl font-bold text-primary mb-3">{article.title}</h1>
          <div className="flex items-center text-sm text-muted-foreground space-x-4">
            <div className="flex items-center">
              <CalendarDays className="mr-2 h-4 w-4" />
              <span>{article.date}</span>
            </div>
            <span>|</span>
            <span>Categoría: {article.category}</span>
          </div>
        </header>
        
        {article.image && (
          <Image 
            src={article.image} 
            alt={article.title} 
            width={800} 
            height={400} 
            className="w-full h-auto rounded-lg mb-8 shadow-md object-cover"
            data-ai-hint={article.aiHint}
             priority
          />
        )}

        <div 
          className="prose prose-lg max-w-none text-foreground/90"
          dangerouslySetInnerHTML={{ __html: article.content }} 
        />

        {/* TODO: Add social share buttons, related articles, comments section */}
      </article>
    </div>
  );
}

